html {
  width: 100%;
  height:100%;
}

body {
  position: absolute;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  background: rgb(7,32,38);
  background: -moz-linear-gradient(-45deg, rgba(7,32,38,1) 0%, rgba(0,0,0,1) 40%, rgba(0,0,0,1) 60%, rgba(7,32,38,1) 100%);
  background: -webkit-linear-gradient(-45deg, rgba(7,32,38,1) 0%,rgba(0,0,0,1) 40%,rgba(0,0,0,1) 60%,rgba(7,32,38,1) 100%);
  background: linear-gradient(135deg, rgba(7,32,38,1) 0%,rgba(0,0,0,1) 40%,rgba(0,0,0,1) 60%,rgba(7,32,38,1) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#072026', endColorstr='#072026',GradientType=1 );
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
}

.bodyOverlay {
  position: absolute;
  width: 100%;
  height: 100%;
  background-image: url(./assets/img/bg_tileable.png);
  background-repeat: repeat;
  background-size: 65%;
  background-position: center;
  opacity: .125;
  overflow: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
