.CubeContainer {
  position: absolute;
  display: flex;
  justify-content: space-around;
  width: 100%;
  height: calc(100% - 195px);
  /*perspective: 25em;*/
}

.cube {
  position: absolute;
  //top: 50%;
  //left: 50%;
  align-self: center;
  transform-style: preserve-3d;
  -webkit-transform-style: preserve-3d;
  -moz-transform-style: preserve-3d;
  backface-visibility: hidden;
  -webkit-backface-visibility: hidden;
  -moz-backface-visibility: hidden;
  transform: rotateY(49deg) rotateX(-22.5deg) rotateZ(-22.5deg);
  transition: all .75s;
  transition-delay: 1.2s;
}

.cube-face {
  position: absolute;
  margin: -6em;
  width: 12em;
  height: 12em;
  background-color: black;
  filter: drop-shadow(0px 0px 1px #086c70);
  animation: boxShadow .3s ease;
  box-shadow: #086c70 inset 0 0 0 6px;
  backface-visibility: hidden;
  -webkit-backface-visibility: hidden;
  -moz-backface-visibility: hidden;
  transform-style: preserve-3d;
  -webkit-transform-style: preserve-3d;
  -moz-transform-style: preserve-3d;
}

@keyframes boxShadow {
  0% {box-shadow: #086c70 inset 0 0 0 0px;}
  100% {box-shadow: #086c70 inset 0 0 0 6px;}
}

.cube-face:nth-child(1) {
  transform: rotate3d(0 /* i */, 1 /* j */, 0 /* k */,   0deg /*  0*90° */)
    translateZ(calc(.5*12em))
}
.cube-face:nth-child(2) {
  transform: rotate3d(0 /* i */, 1 /* j */, 0 /* k */,  90deg /*  1*90° */)
    translateZ(calc(.5*12em))
}
.cube-face:nth-child(3) {
  transform: rotate3d(0 /* i */, 1 /* j */, 0 /* k */, 180deg /*  2*90° */)
    translateZ(calc(.5*12em))
}
.cube-face:nth-child(4) {
  transform: rotate3d(0 /* i */, 1 /* j */, 0 /* k */, 270deg /*  3*90° */)
    translateZ(calc(.5*12em))
}
.cube-face:nth-child(5) {
  transform: rotate3d(1 /* i */, 0 /* j */, 0 /* k */,  90deg /*  1*90° */)
    translateZ(calc(.5*12em))
}
.cube-face:nth-child(6) {
  transform: rotate3d(1 /* i */, 0 /* j */, 0 /* k */, -90deg /* -1*90° */)
    translateZ(calc(.5*12em))
}

.projectsAni {
  animation: turnTop .75s ease-in-out;
  transform: rotateY(0deg) rotateX(-90deg) rotateZ(0deg);
  transition-delay: 0s;
}

.aboutAni {
  animation: turnLeft .75s ease-in-out;
  transform: rotateY(90deg) rotateX(0deg) rotateZ(0deg);
  transition-delay: 0s;
}

.contactAni {
  animation: turnRight .75s ease-in-out;
  transform: rotateY(0deg) rotateX(0deg) rotateZ(0deg);
  transition-delay: 0s;
}

@keyframes turnRight {
  0%   { transform: rotateY(49deg) rotateX(-22.5deg) rotateZ(-22.5deg); }
  100% { transform: rotateY(0deg) rotateX(0deg) rotateZ(0deg); }
}

@keyframes turnLeft {
  0%   { transform: rotateY(49deg) rotateX(-22.5deg) rotateZ(-22.5deg); }
  10% {backface-visiblity:visible}
  20% {backface-visiblity:hidden}
  100% { transform: rotateY(90deg) rotateX(0deg) rotateZ(0deg); }
}

@keyframes turnTop {
  0%   { transform: rotateY(49deg) rotateX(-22.5deg) rotateZ(-22.5deg); }
  100% { transform: rotateY(0deg) rotateX(-90deg) rotateZ(0deg); }
}

@font-face {
  font-family: 'spyagency';
  font-style: normal;
  font-weight: 400;
  src: url('../../assets/fonts/spyagency2_1.ttf');
}

.nav-link {
  color: #10a483;
  font-size: 2vw;
  font-family: 'spyagency';
  cursor: pointer;
  transition: color .3s, box-shadow .2s;
}

.nav-link:hover {
  color: #26d0aa;
  box-shadow: 6px 15px 57px -6px rgba(0,0,0,1);
}

.projects {
  position: absolute;
  top: 95px;
}

.about, .contact {
  align-self: center;
}

.reset {
  z-index: 10;
  position: absolute;
  top: 8px;
  right: 20px;
  font-size: 1.5em;
  font-family: 'spyagency';
  cursor: pointer;
  text-shadow:
 -1px -1px 0 red,
  1px -1px 0 red,
  -1px 1px 0 red,
   1px 1px 0 red;
  color: black;

}

.nav-link-top {
  align-self: unset;
  top:-30px;
  position: relative;
  max-height: 24px;
}

.content {
  display: flex;
  position: absolute;
  /*top: calc(50% - 6em);*/
  align-self: center;
  width: 12em;
  height: 12em;
  box-shadow: #086c70 inset 0 0 0 6px;
  background-color: black;
  z-index: 0;
  padding: 0px;
}

.expandContent {
  animation: animateExpand .75s forwards;
  animation-delay: .90s;
}

.retractContent {
  animation: animateContract .75s forwards;
}

@keyframes animateExpand {
  0% {
    width: 12em;
    height: 12em;
    opacity: 0;
    z-index: 0;
  }
  10% {
    opacity: 1;
    z-index: 1;
  }
  90% {
    width: 96%;
    height: 94%;
  }
  100% {
    opacity: 1;
    z-index: 5;
    /*top: 3%;*/
    width: 96%;
    height: 94%;
    box-shadow: #086c70 0 0 0 6px;
  }
}

@keyframes animateContract {
  0% {
    opacity: 1;
    z-index: 5;
    /*top: 0;*/
    width: 70%;
    height: 100%;
  }
  70% {
    /*top : calc(50% - 6em);*/
    width: 12em;
    height: 12em;
  }
  100% {
    z-index: 0;
    box-shadow: #086c70 inset 0 0 0 6px;
  }
}

.page {
  position: relative;
  padding: 2%;
  color: #e6e6e6;
  line-height: 22px;
  text-align: left;
  -webkit-box-flex: 1;
  -webkit-flex: 1;
  -moz-box-flex: 1;
  -ms-flex: 1;
  flex: 1;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
  -moz-box-orient: vertical;
  -moz-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  animation: pageVis 3.5s linear;
  overflow: auto;
}

@keyframes pageVis {
  0% {opacity: 0;}
  50% {opacity: 0;}
  100% {opacity: 1;}
}

.page a {
  color: #10a483;
}

.proj-img, .about-img {
  float: right;
  max-width: 40%;
  margin-left: 24px;
}

.subPic {
  width: 25%;
}

.synthPic {
  width: 25%;
}

.mtbVid {
  width: 25%;
}

.emailLink {

}

@media only screen and (max-width: 500px) {
  .CubeContainer {
    height: calc(100% - 110px);
  }
  .page {
    padding: 5%;
  }
}

@media only screen and (max-width: 900px) {
  .nav-link {
    align-self: unset;
    top:-32px;
    position: relative;
    max-height: 24px;
    font-size: 3vw;
  }

  @keyframes animateExpand {
    0% {
      width: 12em;
      height: 12em;
      opacity: 0;
      z-index: 0;
    }
    10% {
      opacity: 1;
      z-index: 1;
    }
    90% {
      width: 100%;
      height: 100%;
    }
    100% {
      opacity: 1;
      z-index: 5;
      width: 100%;
      height: 100%;
      box-shadow: rgb(8, 108, 112) 0 0 0 6px;
    }
  }
}
