.Header {
  display: flex;
  width: 100%;
  height: 195px;
}

.App-header {
  display: flex;
  flex-direction: row;
  width: 100%;
  //height: 195px;
  font-size: calc(10px + 2vmin);
  color: white;
}

.header-logo {
  max-width: 175px;
  margin-top: 12px;
  align-self: flex-start;
  position: relative;
  left: 65px;
}

.header-bgImg {
  position: absolute;
  width: 75px;
  top: 75px;
  left: 45px;
}

@font-face {
  font-family: 'spyagency';
  font-style: normal;
  font-weight: 400;
  src: url('../../assets/fonts/spyagency2_1.ttf');
}

.header-title-wrap {
  display: flex;
  position: relative;
  flex-direction: column;
  justify-content: center;
  width: 300px;
  left: 45px;
  max-height: 145px;
}

.header-title {
  position: relative;
  margin: 55px 0 6px 0;
  color: #10a483;
  font-size: 24px;
  font-family: 'spyagency';
  text-shadow: -4px 4px 2px #000;
}

.header-subtitle {
  position: relative;
  color: #10a483;
  font-size: 16px;
  font-family: 'spyagency';
  text-shadow: -4px 4px 2px #000;
}

.headerNav {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  flex-grow: 1;
  align-items: flex-end;
}

@media only screen and (max-width: 500px) {

  .Header {
    height: 120px;
  }

  .header-bgImg {
    left: 0;
    top: 47px;
    width: 50px;
  }

  .header-logo {
    min-width: 100px;
    width: 100px;
    left: 30px;
  }

  .header-title-wrap {
    width: 100%;
    left: 15px;
  }

  .header-title {
    margin-top: -10px;
    align-self: center;
    font-size: 16px;

  }
  .header-subtitle {
    align-self: center;
  }
}
